import { graphql, Link } from "gatsby"
import React, { useState } from "react"
import { connect } from "react-redux"
import styled from "styled-components"
import Bio from "../components/bio"
import Button from "../components/button"
import PageWrapper from "../components/pageWrapper"
import SEO from "../components/seo"
import { formatReadingTime } from "../templates/blog-post"
import { darkTheme, lightTheme } from "../theme"
import { rhythm } from "../utils/typography"

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMdx(sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          timeToRead
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
            description
          }
        }
      }
    }
  }
`

const Card = styled(Link)`
  display: block;
  width: 100%;
  padding: 2.5rem;
  margin-bottom: 2rem;
  position: relative;
  background: ${props => props.theme.cardBackground};
  border-radius: 10px;
  border: 1px solid ${props => props.theme.cardBorder};
  box-shadow: ${props => props.theme.cardBoxShadow};
  will-change: transform;
  color: ${props => props.theme.cardText};
  transition: box-shadow 0.2s ease;

  h3 {
    transition: none;
  }

  &:hover {
    box-shadow: ${props => props.theme.cardBoxShadowHover};
  }

  &::before {
    box-shadow: ${props => props.theme.cardBoxBorder};
  }
`

const CardPosts = props => {
  const { posts, isDarkMode, theme } = props
  const [show, setShow] = useState(false)

  return (
    <>
      {posts.map(({ node }, index) => {
        const title = node.frontmatter.title || node.fields.slug
        const timeToRead = formatReadingTime(node.timeToRead)

        if (index < 5 || show) {
          return (
            <Card
              key={node.fields.slug}
              to={`blog${node.fields.slug}`}
              theme={theme}
            >
              <h3
                style={{
                  marginTop: 0,
                  marginBottom: rhythm(1 / 4),
                }}
              >
                {title}
              </h3>
              <small>
                <span>{node.frontmatter.date}</span>
                <span>&nbsp;  &nbsp;</span>
                <span>{timeToRead}</span>
              </small>
              <p
                style={{
                  margin: "0 auto",
                }}
                dangerouslySetInnerHTML={{
                  __html: node.frontmatter.description || node.excerpt,
                }}
              />
            </Card>
          )
        }

        return null
      })}
      {posts.length >= 5 && !show && (
        <Button
          marginTop="48px"
          background={isDarkMode && "#5e6ad2"}
          center
          onClick={() => {
            setShow(true)
          }}
        >
           show all posts
        </Button>
      )}
    </>
  )
}

const Blog = props => {
  const { data, location, isDarkMode } = props
  const posts = data.allMdx.edges
  const siteTitle = data.site.siteMetadata.title
  const theme = isDarkMode ? darkTheme : lightTheme

  return (
    <PageWrapper location={location} title={siteTitle}>
      <SEO title="Blog" url="https://www.vitormalencar.com/blog/" />
      <Bio />
      <div style={{ margin: `20px 0 ${rhythm(2)}` }}>
        <CardPosts posts={posts} isDarkMode={isDarkMode} theme={theme} />
      </div>
      <hr style={{ marginBottom: rhythm(2) }} />
    </PageWrapper>
  )
}

const mapStateToProps = state => ({
  isDarkMode: state.theme.isDarkMode,
})

export default connect(mapStateToProps)(Blog)
